@font-face {
  font-family: "Favorit Extended";
  src: url("./../fonts/Favorit-ExtendedBold.eot");
  src: url("./../fonts/Favorit-ExtendedBold.eot?#iefix")
      format("embedded-opentype"),
    url("./../fonts/Favorit-ExtendedBold.woff2") format("woff2"),
    url("./../fonts/Favorit-ExtendedBold.woff") format("woff"),
    url("./../fonts/Favorit-ExtendedBold.ttf") format("truetype"),
    url("./../fonts/Favorit-ExtendedBold.svg#Favorit-ExtendedBold")
      format("svg");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

