}
@font-face {
  font-family: "Favorit Extended";
  src: url("./../fonts/Favorit-ExtendedRegular.eot");
  src: url("./../fonts/Favorit-ExtendedRegular.eot?#iefix") format("embedded-opentype"), url("./../fonts/Favorit-ExtendedRegular.woff2") format("woff2"), url("./../fonts/Favorit-ExtendedRegular.woff") format("woff"), url("./../fonts/Favorit-ExtendedRegular.ttf") format("truetype"), url("./../fonts/Favorit-ExtendedRegular.svg#Favorit-ExtendedRegular") format("svg");
  font-weight: normal;
