}
.svg-image-right, .svg-image-left {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-left {
  background-position: 0 100%;
  background-size: 101.1453744493% auto;
  width: 1135px;
}

.svg-image-left:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 34.8898678414%;
}

.svg-image-left-dims {
  width: 1135px;
  height: 396px;
}

.svg-image-right {
  background-position: 0 0;
  background-size: 100% auto;
  width: 1148px;
}

.svg-image-right:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 35.0174216028%;
}

.svg-image-right-dims {
  width: 1148px;
  height: 402px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

:root {
  --white-color: #fff;
  --black-color: #000;
  --favorit: "Favorit Extended", sans-serif;
  --inter: "Inter", sans-serif;
  --transition: all 0.2s ease-out;
}

html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

*,
h1,
h2,
h3,
p {
  padding: 0;
  margin: 0;
}

ul,
ol,
li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: var(--favorit);
  color: var(--white-color);
  background-color: var(--black-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1366px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 24px;
}

.header {
  height: 58px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.header__panel {
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__panel li:not(:last-child) {
  margin-right: 4px;
}

.header__panel--left {
  justify-content: flex-start;
  padding-left: 6.9%;
}

.header__panel--right {
  justify-content: flex-end;
}

.header__link {
  padding: 8px 16px;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-transform: uppercase;
  transition: var(--transition);
}
.header__link:hover {
  opacity: 0.7;
}

.main {
  flex-grow: 1;
}

.intro {
  padding-top: 40px;
  border-top: 1px solid var(--white-color);
}

.intro__top {
  position: absolute;
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
  min-width: 50px;
  padding: 12px 22px 13px;
  font-family: var(--inter);
  font-weight: 500;
  font-size: 34px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--white-color);
  background-color: var(--black-color);
}
.intro__top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 28px;
  border-radius: 15px 15px 0 0;
  border: 1px solid var(--white-color);
  border-bottom: none;
}

.intro__top--left,
.intro__top--right {
  position: absolute;
  top: 28px;
  width: 14px;
  height: 15px;
  background-color: var(--black-color);
}
.intro__top--left::before,
.intro__top--right::before {
  content: "";
  position: absolute;
  top: 0;
  width: 16px;
  height: 100%;
  background-color: var(--black-color);
}

.intro__top--left {
  left: -13px;
}
.intro__top--left::before {
  left: -2px;
  border-right: 1px solid var(--white-color);
  border-bottom: 1px solid var(--white-color);
  border-radius: 0 0 15px 0;
}

.intro__top--right {
  right: -13px;
}
.intro__top--right::before {
  right: -2px;
  border-left: 1px solid var(--white-color);
  border-bottom: 1px solid var(--white-color);
  border-radius: 0 0 0 15px;
}

.intro__wrapper {
  position: relative;
  width: 100%;
  min-height: 518px;
}

.intro__main {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  background-color: var(--black-color);
  border: 1px solid var(--white-color);
  border-radius: 30px 30px 0 0;
  overflow: hidden;
}

.intro__content,
.intro__visual {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.intro__content {
  z-index: 10;
  padding: 68px 56px;
}

.intro__visual {
  z-index: 5;
}

.intro__bg--left {
  position: absolute;
  top: 0;
  left: 0;
  width: 86.12%;
  padding-top: 30.05%;
}

.svg-image-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
}

.intro__bg--left {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 87.1%;
  padding-top: 30.5%;
}

.svg-image-right {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: auto;
}

.intro__orange {
  position: absolute;
  bottom: -43px;
  right: 112px;
  z-index: 20;
  width: 405px;
  height: 405px;
}
.intro__orange img {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.intro__purple {
  position: absolute;
  bottom: 145px;
  right: 28px;
  z-index: 10;
  width: 286px;
  height: 286px;
}
.intro__purple img {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.intro__suptitle {
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  opacity: 0.5;
}

.intro__title {
  margin-bottom: 24px;
  font-weight: 350;
  font-size: 56px;
  line-height: 64px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
}

.intro__text {
  margin-bottom: 32px;
  font-weight: 350;
  font-size: 16px;
  line-height: 64px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
}

.intro__button {
  padding: 18px 32px;
  border: none;
  border-radius: 0px 20px 20px 32px;
  font-weight: 700;
  font-size: 16px;
  line-height: 125%;
  color: var(--black-color);
  background: var(--white-color);
  outline: none;
  appearance: none;
  cursor: pointer;
  transition: var(--transition);
}
.intro__button:hover {
  opacity: 0.7;
}

.intro__nav {
  position: absolute;
  bottom: -27px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  width: 162px;
  height: 42px;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.intro__prev,
.intro__center,
.intro__next {
  display: flex;
  align-items: center;
  background-color: var(--black-color);
  outline: none;
  appearance: none;
  cursor: pointer;
  transition: all 0.1s ease-out;
}
.intro__prev path,
.intro__center path,
.intro__next path {
  transition: var(--transition);
}
.intro__prev:hover path,
.intro__center:hover path,
.intro__next:hover path {
  fill: #b2b2b2;
}

.intro__center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  width: 64px;
  height: 64px;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid var(--white-color);
}
.intro__center:active {
  transform: translate(-50%, -50%) scale(0.9);
}

.intro__prev,
.intro__next {
  width: 50%;
  border: 1px solid var(--white-color);
}
.intro__prev:active,
.intro__next:active {
  transform: scale(0.9);
}

.intro__prev {
  justify-content: flex-start;
  padding-left: 21px;
  border-radius: 30px 0 0 30px;
}

.intro__next {
  justify-content: flex-end;
  padding-right: 21px;
  border-radius: 0 30px 30px 0;
}

footer {
  padding: 20px;
}